@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.wrap-forms {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.form {
    padding: 20px 16px;
    width: 280px;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border-radius: 4px;
}

.form__group {
    min-height: 60px;
}

.form__input {
    position: relative;
    width: 100%;
    height: 42px;
    border-radius: 4px;
    background: #FFFFFF;
    border: 1px solid #E6ECEF;
    padding-left: 14px;
    color: #868A8D;
    transition: all 0.3s ease-out;
}
.form__select {
    position: relative;
    width: 100%;
    height: 42px;
    color: #868A8D;
    transition: all 0.3s ease-out;
}
.form__input:hover,
.form__input:focus {
    border-color: #6d6f71;
}

.form__button {
    width: 100%;
    height: 42px;
    border-radius: 4px;
}

.label-error {
    display: inline-block;
    font-size: 11px;
    color: red;
    padding: 0 14px 2px;
    transition: all 0.2s ease-in;
}

@media screen and (max-width: 600px) {
    .wrap-forms {
        display: block;
    }
    .form {
        width: 100%;
        margin: auto;
    }
    .form:nth-child(2) {
        margin: 20px auto 0;
    }
}

.modal {
    width: 100%;
    height: 100%;
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    right: 0;
    background: rgba(0,0,0,0.6);
    z-index: 50;
    pointer-events: none;
    opacity: 0;
    transition: 0.5s;
    overflow: auto;
}
.modal__content {
    width: 280px;
    transform: scale(0.5);
    transition: 0.4s all;
}
.modal_active {
    pointer-events: all;
    opacity: 1;
}
.modal__content_active {
    transform: scale(1);
}
@media screen and (max-width: 600px) {
    .modal__content {
        padding: 0 10px;
        width: 100%;
    }
}

.table-wrap {
    margin-top: 45px;
}

.table {
    width: 100%;
    max-width: 100%;
    margin-top: 16px;
}

.table__row-header {
    background: #0A508B;
}
.table__th {
    font-weight: 400;
    color: rgba(255,255,255,.5);
    padding: 9px 12px;
    text-align: left;
}
.table__th:first-child {
    border-top-left-radius: 3px;
}
.table__th:last-child {
    border-top-right-radius: 3px;
}

.table__row {
    color: #868A8D;
}
.table__row_font_italic {
    font-style: italic;
}

.table__td {
    padding: 12px;
    border-right: 1px solid #EBEBEB;
    border-bottom: 1px solid #EBEBEB;
    background: #fff;
    vertical-align: middle;
}
.table__td:first-child {
    padding: 12px;
    width: 16%;
}
.table__td:nth-child(2) {
    width: 22%;
}
.table__td:nth-child(3) {
    width: 16%;
}
.table__td:nth-child(4) {
    width: 14%;
}
.table__td:last-child {
    width: 32%;
    padding: 9px 0;
    border-right: 0px solid #EBEBEB;
}
.table__cell-btns {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;

    text-decoration: underline;
}

.table__row-edit {
    color: #057AE3;
}
.table__row-del {
    color: #B0263D;
    background:none;
    border:none;
    margin:0;
    padding:0;
    cursor: pointer;
}
@media screen and (max-width: 400px){
    .table__cell-btns {
        flex-direction: column;
    }
    .table__row-del {
        margin-top: 10px;
    }
}

.table-btns {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 4px;
}

.table-btns__copy-table {
    width: 90px;
    height: 24px;
    border-radius: 2px;
    margin-left: 25px;
}
.table-btns__cross {
    margin-left: 26px;
    width: 14px;
    height: 14px;
}


.cross {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: 0;
    background: transparent;
    position: relative;
    transform: scale(1);
    transition: all 0.2s ease-in;
}
.cross:hover {
    transform: scale(1.1);
}
.cross:active {
    transform: scale(0.9);
}
.cross:before,
.cross:after {
    content: "";
    position: absolute;
    z-index: -1;
    background: #EB5E58;
    width: 20px;
    height: 2px;
    border-radius: 1px;
    top: 45%;
    right: -20%;
}
.cross:before {
    transform: rotate(45deg);
}
.cross:after {
    transform: rotate(-45deg);
}

.button {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    border: 0;
    background: #1493FE;
    color: #fff;
    text-decoration: none;
    transition: all 0.2s ease-in;
}
.button:hover {
    background: #3ca7ff;
}
.button:active {
    background: #0084f3;
}
.button[disabled] {
    cursor: no-drop;
    background: #C2CBD1;
}
.button[disabled]:hover {
    cursor: no-drop;
    background: #C2CBD1;
}
.button[disabled]:active {
    cursor: no-drop;
    background: #C2CBD1;
}

.button_font-weight_bold {
    font-weight: bold;
}

.select {

}

.select_active .select__value::before {
    transform: translate(-3px, -50%) rotate(-45deg);
}

.select_active .select__value::after {
    transform: translate(1px, -50%) rotate(45deg);
}


.select_active .select__list {
    opacity: 1;
}

.select_active .select__option {
    max-height: 40px;
    border-top-width: 1px;
}

.select__value {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 8px 16px;
    border-radius: 5px;
    border: solid 1px #E6ECEF;
    cursor: pointer;
    transition: all 0.3s ease-out;
}
.select__value::before,
.select__value::after {
    content: "";

    position: absolute;
    top: 50%;
    right: 16px;

    display: block;
    width: 6px;
    height: 1px;

    transition: all 0.3s ease-out;

    background-color: #6F7B8D;
    border-radius: 1px;

    transform: translate(-3px, -50%) rotate(45deg);
}
.select__value::after {
    transform: translate(1px, -50%) rotate(-45deg);
}

.select__value:hover {
    border-color: #6d6f71;
}

.select__list {
    position: absolute;
    top: 44px;
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #ffffff;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.08);
    border-radius: 4px;
    transition: all 0.3s ease-out;
    opacity: 0;
    z-index: 8;
}


.select__option {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 40px;
    max-height: 0;
    padding: 0 16px;
    transition: all 0.2s ease-out;
    cursor: pointer;
    overflow: hidden;
}

.select__option:hover {
    color: #212229;
    font-weight: bold;
}

.select__option::after {
    content: '';
    position: absolute;
    width: calc(100% - 32px);
    height: 1px;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
    background: #E6ECEF;
}
.select__option:first-child::after {
    display: none;
}

html, body {
    font-family: Roboto, Arial, sans-serif;
    font-size: 12px;
    line-height: 1.17;
    font-weight: normal;
    background: #E5E8ED;
    margin: 0;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
* {
    box-sizing: border-box;
}

form, label, input, select, option,
ul, li,
table, caption, tbody, tfoot, thead, tr, th, td {
    margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline;
    font: inherit;
}
a, button {
    cursor: pointer;
}
.wrap {
    padding: 10px;
    position: relative;
    width: 800px;
    margin: auto;
}
@media screen and (max-width: 800px) {
    .wrap {
        width: 100%;
    }
}

